import React from 'react'
import PropTypes from 'prop-types'
import AniLink from 'gatsby-plugin-transition-link/AniLink'
import styled from 'styled-components'
import Img from 'gatsby-image'

const StyledLink = styled(AniLink)`
  text-decoration: none;
  color: black;
`

const StyledImg = styled(Img)`
  display: none;
  margin-bottom: 1rem;

  @media (max-width: 768px) {
    display: block;
  }
`

const PostCard = ({ post, handleImageChange, idx }) => {
  const url = `/blog/${post.slug}/`
  const { created_at_pretty, title, localFeatureImage } = post

  return (
    <div
      className="post"
      onMouseOver={() => handleImageChange(idx)}
      onFocus={() => handleImageChange(idx)}
      role="link"
      tabIndex={idx}
    >
      <article>
        <StyledLink
          cover
          bg={'black'}
          direction="right"
          to={url}
          className="post-card"
        >
          <StyledImg fluid={localFeatureImage.childImageSharp.fluid} />
          <span className="post__meta">{created_at_pretty}</span>
          <h2 className="post__title">{title}</h2>
          <span className="post__cta">Read More</span>
        </StyledLink>
      </article>
    </div>
  )
}

PostCard.propTypes = {
  post: PropTypes.shape({
    slug: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    feature_image: PropTypes.string,
    featured: PropTypes.bool,
    tags: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string,
      }),
    ),
    excerpt: PropTypes.string.isRequired,
    primary_author: PropTypes.shape({
      name: PropTypes.string.isRequired,
      profile_image: PropTypes.string,
    }).isRequired,
  }).isRequired,
}

export default PostCard
